import React, {useState, useEffect}  from 'react';
import {useDispatch, useSelector}    from 'react-redux';
import {Link, useHistory, useParams} from 'react-router-dom';

import {connect} from 'react-redux';
import {toast}   from 'react-toastify';
import {addList} from '../components/redux/actions';

export const Edit = () => {

	const {id} = useParams();

	const [firstName, setfname]     = useState('');
	const [lastName, setlname]      = useState('');
	const [mail, setmail]           = useState('');
	const [number, setnumber]       = useState('');
	const [address1, setadd1]       = useState('');
	const [address2, setadd2]       = useState('');
	const [city, setcity]           = useState('');
	const [state, setstate]         = useState('');
	const [zipCode, setzip]         = useState('');
	const [country, setcnty]        = useState('');
	const [qualification, setqufcn] = useState('');
	const [Comments, setcmt]        = useState('');

	const list        = useSelector(state => state);
	const currentlist = list.find((list) => list.id === parseInt(id));
	const dispatch    = useDispatch();
	const history     = useHistory();

	useEffect(() => {
		if(currentlist){
			setfname(currentlist.firstName);
			setlname(currentlist.lastName);
			setmail(currentlist.mail);
			setnumber(currentlist.number);
			setadd1(currentlist.address1);
			setadd2(currentlist.address2);
			setcity(currentlist.city);
			setstate(currentlist.state);
			setzip(currentlist.zipCode);
			setcnty(currentlist.country);
			setqufcn(currentlist.qualification);
			setcmt(currentlist.Comments);
		}
	}, [currentlist]);

	const handleSubmit = (e) => {
		e.preventDefault();

		const checkmail   = list.find(list => list.id !== parseInt(id) && list.mail === mail);
		const checknumber = list.find(list => list.id !== parseInt(id) && list.number === parseInt(number));

		if(!mail || !number){
			return toast.warning('please Fill in Mail & Number fields');
		}

		if(checkmail){
			toast.error('email already Exists');
		}
		if(checknumber){
			toast.error('Number already Exists');
		}
		const data = {
			id: parseInt(id),
			firstName,
			lastName,
			mail,
			number,
			address1,
			address2,
			city,
			state,
			zipCode,
			country,
			qualification,
			Comments,
		};
		dispatch({type: 'UPDATE_LIST', payload: data});
		toast.success('User value Update');
		history.push('/');
	};

	return (
		<div className="container">
			{
				currentlist ? (
					<div className="box">
						<div className="top-head mt-2">
							<Link to="/" className='my-3'>&#8592; Back</Link>
							<h1 className="mb-3 mt-3">Edit contect {id}</h1>

						</div>

						<form onSubmit={handleSubmit}>
							<div className="row">
								<div className="col-sm-12 col-md-6  mb-4">
									<label className="form-label"> firstName</label>
									<input
										type="text"
										className="form-control"
										placeholder="firstName"
										value={firstName}
										onChange={e => setfname(e.target.value)}/>
								</div>
								<div className="col-sm-12 col-md-6  mb-4">
									<label className="form-label"> lastName</label>
									<input
										type="text"
										className="form-control"
										placeholder="lastName"
										value={lastName}
										onChange={e => setlname(e.target.value)}
									/>
								</div>
								<div className="col-sm-12 col-md-6  mb-4">
									<label className="form-label"> Mail</label>
									<input
										type="mail"
										className="form-control"
										placeholder="mail"
										value={mail}
										onChange={e => setmail(e.target.value)}/>
								</div>
								<div className="col-sm-12 col-md-6  mb-4">
									<label className="form-label"> phoneNumber</label>
									<input
										type="text"
										className="form-control"
										placeholder="phoneNumber"
										value={number}
										onChange={e => setnumber(e.target.value)}
									/>
								</div>
								<div className="col-sm-12 col-md-6  mb-4">
									<label className="form-label"> address1</label>
									<input
										type="text"
										className="form-control"
										placeholder="address1"
										value={address1}
										onChange={e => setadd1(e.target.value)}
									/>
								</div>
								<div className="col-sm-12 col-md-6  mb-4">
									<label className="form-label"> address2</label>
									<input
										type="text"
										className="form-control"
										placeholder="address2"
										value={address2}
										onChange={e => setadd2(e.target.value)}
									/>
								</div>
								<div className="col-sm-12 col-md-6 col-lg-3 mb-4">
									<label className="form-label"> city</label>
									<input
										type="text"
										className="form-control"
										placeholder="city"
										value={city}
										onChange={e => setcity(e.target.value)}
									/>
								</div>
								<div className="col-sm-12 col-md-6 col-lg-3 mb-4">
									<label className="form-label"> state</label>
									<input
										type="text"
										className="form-control"
										placeholder="state"
										value={state}
										onChange={e => setstate(e.target.value)}
									/>
								</div>
								<div className="col-sm-12 col-md-6 col-lg-3 mb-4">
									<label className="form-label"> zipCode</label>
									<input
										type="text"
										className="form-control"
										placeholder="zipCode"
										value={zipCode}
										onChange={e => setzip(e.target.value)}
									/>
								</div>
								<div className="col-sm-12 col-md-6 col-lg-3 mb-4">
									<label className="form-label"> country</label>
									<input
										type="text"
										className="form-control"
										placeholder="country"
										value={country}
										onChange={e => setcnty(e.target.value)}
									/>
								</div>
								<div className="col-sm-12 col-md-6  mb-4">
									<label className="form-label"> qualification</label>
									<input
										type="text"
										className="form-control"
										placeholder="qualification"
										value={qualification}
										onChange={e => setqufcn(e.target.value)}
									/>
								</div>
								<div className="col-sm-12 col-md-6  mb-4">
									<label className="form-label"> Comments</label>
									<input
										type="text"
										className="form-control"
										placeholder="Comments"
										value={Comments}
										onChange={e => setcmt(e.target.value)}
									/>
								</div>
								<div className="d-grid gap-2">
									<input type="submit" value="Submit" className="btn-primary"/>
								</div>
							</div>
						</form>
					</div>
				) : (
					<h1>User Id {id} not Exists</h1>
				)}

		</div>
	);
};
