//import {ADD_LIST,UPDATE_LIST,DELETE_LIST } from './actions'
import {list} from './states';

export let reducer = (state = list, action) => {
	switch(action.type){
		case 'ADD_LIST':
			state = [...state, action.payload];
			return state;
		case 'UPDATE_LIST':
			const updatestate = state.map(list => list.id === action.payload.id ? action.payload : list);
			state             = updatestate;
			return state;
		case 'DELETE_LIST':
			const deletestate = state.filter((list) => list.id !== action.payload ? list : null);
			state             = deletestate;
			return state;
		default:
			return state;
	}

};
