import React                      from 'react';
import {useDispatch, useSelector} from 'react-redux';

import {Link} from 'react-router-dom';

export const View = () => {

	const lists    = useSelector(state => state);
	const dispatch = useDispatch();

	const deleteList = (id) => {
		dispatch({type: 'DELETE_LIST', payload: id});
	};

	return (
		<div className="container-fluid container-lg">
			<div className="listing">
				<div className="row list-head mb-3 mt-3">
					<div className="col-8">
						<h2>Add User</h2>
					</div>
					<div className="col-4">
						<Link to="/add" className="btn btn-primary">Add User</Link>
					</div>
				</div>
				<div className="row">
					<div className="col-12">
						<table className="table table-striped">
							<thead className="text-white bg-dark text-center">
							<tr>
								<th scope="col">Id</th>
								<th scope="col">
									<div className="table-col wrap-nm">
										<span>FirstName</span>
										<span>LastName</span>
									</div>
								</th>
								<th scope="col">Email</th>
								<th scope="col">PhoneNumber</th>
								<th scope="col">Address</th>
								<th scope="col">Qualification</th>
								<th scope="col">Comments</th>
								<th scope="col">Action</th>
							</tr>
							</thead>
							<tbody>
							{lists.map((lists, id) => (
								<tr key={id}>
									<td data-label="Id">{id + 1}</td>
									<td data-label="Name">
										<div className="table-col">
											<span>{lists.firstName}</span>
											<span>{lists.lastName}</span>
										</div>
									</td>
									<td data-label="Email">{lists.mail}</td>
									<td data-label="PhoneNumber">{lists.number}</td>
									<td data-label="Address">
										<div className="table-col">
											<span>{lists.address1},</span>
											<span>{lists.city},{lists.state},{lists.zipCode},</span>
											<span>{lists.country}</span>
										</div>
									</td>
									<td data-label="Qualification">{lists.qualification}</td>
									<td data-label="Comments">{lists.comments}</td>
									<td data-label="Action">
										<Link to={`/edit/${lists.id}`} className="btn btn-small btn-primary mr-2">Edit</Link>
										<button className="btn btn-small btn-danger mx-3" onClick={() => deleteList(lists.id)}>Delete</button>
									</td>
								</tr>
							))
							}

							<td>

							</td>
							</tbody>
						</table>
					</div>
				</div>
			</div>
		</div>
	);
};
