import React  from 'react';
import {Link} from 'react-router-dom';

export const Navbar = () => {
	return (
		<nav class="navbar navbar-dark bg-dark">
			<div class="container-fluid">
				<Link to="/" className="navbar-brand">
					<strong>Home</strong>
				</Link>
			</div>
		</nav>
	);
};

